import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-vitals',
  templateUrl: './vitals.component.html',
  styleUrls: ['./vitals.component.css']
})
export class VitalsComponent implements OnInit {

  constructor() { }

  ngOnInit() {


    $(function($) {
      $("li").click(function(){
        var newVal1 = $(this).text();
        
        if(newVal1=="General Vitals"){
          
          $("#general-vitals").show(450);
          $("#cardiac-vitals").hide();
          $("#pediatric-vitals").hide();
          $("#er-vitals").hide();
          $("#ot-parameters").hide();
        }
        else if(newVal1=="Cardiac Vitals"){
          
          $("#general-vitals").hide();
          $("#cardiac-vitals").show(450);
          $("#pediatric-vitals").hide();
          $("#er-vitals").hide();
          $("#ot-parameters").hide();
         }
       else if(newVal1=="Pediatric Vitals"){
       
        
        $("#general-vitals").hide();
        $("#cardiac-vitals").hide();
        $("#pediatric-vitals").show(450);
        $("#er-vitals").hide();
        $("#ot-parameters").hide();
       }
       else if(newVal1=="ER Vitals"){

        
        $("#general-vitals").hide();
        $("#cardiac-vitals").hide();
        $("#pediatric-vitals").hide();
        $("#er-vitals").show(450);
        $("#ot-parameters").hide();
       }
       else if(newVal1=="OT Parameters"){

        
        $("#general-vitals").hide();
        $("#cardiac-vitals").hide();
        $("#pediatric-vitals").hide();
        $("#er-vitals").hide();
        $("#ot-parameters").show(450);
       }



      });
    
    });

  
  }

}
