import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AllergyComponent } from './allergy/allergy.component';
import { VitalsComponent } from './vitals/vitals.component';
import { Orders1Component } from './orders1/orders1.component';
import { HistComponent } from './hist/hist.component';
import { DiagnosisComponent } from './diagnosis/diagnosis.component';


const routes: Routes = [
  { path: '', component: VitalsComponent },
  { path: 'vitals', component: VitalsComponent },
  { path: 'allergy', component: AllergyComponent },
  { path: 'orders1', component: Orders1Component },
  { path: 'hist', component: HistComponent },
  { path: 'diagnosis', component: DiagnosisComponent }

];

@NgModule({
  imports: [RouterModule.forRoot(routes)],
  exports: [RouterModule]
})
export class AppRoutingModule { }
